<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-outline-primary mt-4" @click.prevent="openModal(true)"></button>
    </div>
    <table class="table mt-4" style="font-size:1rem;">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td style="text-align:center;">
            <span v-if="item.is_enabled" class="badge badge-success"></span>
            <span v-else class="badge badge-light"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary" @click="openModal(false, item)"></button>
          </td>
          <td>
            <button class="btn btn-outline-danger" @click="openDelModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :compo-pages="pagination" @turn="getProducts"></Pagination>
    <!-- Bootstrap edit Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <!--  -->
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.image"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.image"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <!-- 10, 1true-value, 0 -->
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click.prevent="updateProducts"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      style="font-size:1.6rem;"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">{{tempProduct.title}}</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteProducts()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getProduct_PAGE,
  updateProduct,
  deleteProduct,
  newProduct
} from "@/api/product";

// import ProductModal from "../modal/ProductModal";
import Pagination from "../Pagination";
import $ from "jquery";
export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isNew: false, //
      isLoading: false,
      status: {
        fileUploading: false
      },
      pagination: {}
    };
  },
  components: {
    // ProductModal
    Pagination
  },
  created() {
    this.getProducts();
  },
  methods: {
    getProducts(page = 1) {
      let vm = this;
      vm.isLoading = true;
      let data = {
        page
      };
      getProduct_PAGE(data).then(response => {
        console.log(response.data);
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    // 
    openModal(isNew, item) {
      console.log(isNew, item);
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    //
    openDelModal(item) {
      this.tempProduct = Object.assign({}, item);
      $("#deleteModal").modal("show");
    },
    //
    updateProducts() {
      let vm = this;
      console.log(vm.isNew);
      if (!vm.isNew) {
        // ,
        updateProduct(vm.tempProduct).then(response => {
          $("#productModal").modal("hide");
          if (response.data.success) {
            vm.getProducts();
          } else {
            vm.getProducts();
            console.log("");
          }
        });
      } else {//
        newProduct(vm.tempProduct).then(response => {
          $("#productModal").modal("hide");
          if (response.data.success) {
            vm.getProducts();
          } else {
            vm.getProducts();
            console.log("");
          }
        });
      }
    },
    //(cors)
    uploadFile() {
      // const uploadedFile = this.$refs.files.files[0];
      // const vm = this;
      // const formData = new FormData();
      // formData.append("file-to-upload", uploadedFile);
      // const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_COUSTOMERPATH}/admin/upload`;
      // vm.status.fileUploading = true;
      // axios
      //   .post(url, formData, {
      //     headers: {
      //       "Content-type": "multipart/form-data"
      //     }
      //   })
      //   .then(response => {
      //     vm.status.fileUploading = false;
      //     console.log(response.data);
      //     if (response.data.success) {
      //       vm.tempProduct.imageUrl = response.data.imageUrl;
      //       vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
      //       console.log(response.data);
      //     } else {
      //       this.$bus.$emit("message:push", response.data.message, "danger");
      //     }
      //   });
    },
    //
    deleteProducts() {
      const vm = this;
      deleteProduct(vm.tempProduct).then(response => {
        if (response.data.success) {
          $("#deleteModal").modal("hide");
          vm.getProducts();
          this.$bus.$emit("message:push", response.data.message, "success");
        } else {
          $("#deleteModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    // emit
    getChildText(value) {
      console.log(value);
    }
  }
};
</script>

