<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date | date }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openCouponModal(false, item)"></button>
          </td>
          <td>
            <button class="btn btn-outline-danger btn-sm" @click="deleteCoupon(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                v-model="tempCoupon.title"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input
                type="text"
                class="form-control"
                id="coupon_code"
                v-model="tempCoupon.code"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date" v-model="due_date" />
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input
                type="number"
                class="form-control"
                id="price"
                v-model="tempCoupon.percent"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled"
                  id="is_enabled"
                />
                <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { getCoupons, updateCoupon, addCoupon, delCoupon } from "@/api/coupons";

export default {
  data() {
    return {
      coupons: {},
      tempCoupon: {
        title: "",
        is_enabled: 0,
        percent: 100,
        due_date: 0,
        code: "",
      },
      due_date: new Date(),
      isNew: false,
      isLoading: false,
    };
  },
  watch: {
    due_date() {
      // => ex: xxxx/xx/xx
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    },
  },
  methods: {
    openCouponModal(isNew, item) {
      const vm = this;
      $("#couponModal").modal("show");
      vm.isNew = isNew; // 
      if (vm.isNew) {
        vm.tempCoupon = {};
      } else {
        vm.tempCoupon = Object.assign({}, item);
        const dateAndTime = new Date(vm.tempCoupon.due_date * 1000)
          .toISOString()
          .split("T");
        vm.due_date = dateAndTime[0];
      }
    },
    getCoupon() {
      const vm = this;
      vm.isLoading = true;
      getCoupons(vm.tempProduct).then((response) => {
        vm.coupons = response.data.coupons;
        console.log(response);
        vm.isLoading = false;
      });
    },
    updateCoupon() {
      const vm = this;
      if (vm.isNew) {
        addCoupon(vm.tempCoupon).then((response) => {
          console.log(response);
          $("#couponModal").modal("hide");
          vm.getCoupon();
        });
      } else {
        vm.due_date = new Date(vm.tempCoupon.due_date * 1000);
        updateCoupon(vm.tempCoupon).then((response) => {
          console.log(response);
          $("#couponModal").modal("hide");
          vm.getCoupon();
        });
      }
    },
    deleteCoupon(item) {
      const vm = this;
      vm.isLoading = true;
      delCoupon(item).then((response) => {
        vm.$bus.$emit("message:push", response.data.message, "primary");
        vm.isLoading = false;
        vm.getCoupon();
      });
    },
  },
  created() {
    this.getCoupon();
  },
};
</script>